






















import { Component, Prop, Vue, Watch } from "vue-property-decorator"
import Portal from "@/managers/portal"
import Profile from "@/managers/profile"
import {
	IBooking,
	IStudentProfile,
	ISubscription,
	ITrainerProfile,
	ITrainingSession,
	TrainingSessionDuration,
	TrainingSessionType
} from "@chaosinsight/yogipeople-portalclient"
import PageBackground from "@/components/PageBackground.vue"
import MenuAside from "@/components/MenuAside.vue"
import MenuItems from "@/components/MenuItems.vue"
import StudentBookings from "@/components/StudentBookings.vue"
import HeroText from "@/components/HeroText.vue"
import TrainLive from "@/components/TrainLive.vue"
import TrainSpecific from "@/components/TrainSpecific.vue"
import BookingCountdown from "@/components/BookingCountdown.vue"
import HyModalwithyellowstripe from "@/components/HyModalwithyellowstripe.vue"
import FeedbackStudent from "@/components/FeedbackStudent.vue"
import PageBgRotator from "@/components/PageBgRotator.vue"
import i18n from "../i18n"
import { TranslateResult } from "vue-i18n"

@Component({
	components: {
		BookingCountdown,
		FeedbackStudent,
		HyModalwithyellowstripe,
		MenuAside,
		MenuItems,
		StudentBookings,
		TrainLive,
		TrainSpecific,
		PageBackground,
		PageBgRotator,
		HeroText
		}
})
export default class StudentLanding extends Vue {
	@Prop()
	public profile!: IStudentProfile
	@Prop()
	public feedbackType!: string
	@Prop()
	public sessionData!: ITrainingSession

	@Prop()
	public blur!: boolean

	@Prop()
	public subscriptionCode: string = ""
	public systemTime: string | null = null
	public booking: IBooking | null = null
	public plan: string = "subscription-a"
	private trainer: ITrainerProfile | null = null
	private planData: string = ""
	private planTitle: string = ""
	private planPrice: string = ""
	private planSession: string = ""
	private selectedBookingId: number | null = null
	private selectedTrainerId: string | null = null
	private canceledBookingId: number | null = null
	private listBookings: boolean | null = null
	private prevTrainers: boolean | null = null
	private menuOpen: boolean | null = null
	private activeItem: string | null = null
	private modal: boolean | null = null
	private modalType: string | null = null
	private modalText: TranslateResult | null = null
	private modalSlot: TranslateResult | null = i18n.t(
		"studentLanding.modalSlot"
	)
	private smPanels: boolean | null = null
	private reloadSessionsHistory: boolean | null = null
	private menuItems: object[] = [
		{ label: i18n.t("myAccount"), name: "account" },
		{ label: "My Subscription", name: "subscriptions" },
		{ label: i18n.t("myPayment"), name: "payment" },
		{ label: i18n.t("mySessions"), name: "sessions" },
		{ label: i18n.t("help"), name: "help" },
		{ label: i18n.t("legal"), name: "legal" },
		{ label: i18n.t("signout"), name: "signout" }
	]
	private get isEmailVerified(): boolean | null {
		return Profile.isReady ? Profile.profile!.IsMobileVerified : null
	}
	@Watch("feedbackType")
	private feedback(value: string): void {
		if (!!value) this.modal = true
		}

	@Watch("trainer")
	private showSmallPanel(value: ITrainerProfile): void {
		if (value) this.smPanels = true
		}
	private created(): void {
		this.getSystemTime().then(time => (this.systemTime = time))
		this.planData = this.$route.fullPath.split("=")[1]
		window.localStorage.setItem("plan", this.planData)
		if (this.planData === "basic") {
			this.planTitle = "Basic"
			this.planPrice = "195 Kr"
			this.planSession = "2" } else if (this.planData === "standard") {
			this.planTitle = "Standard"
			this.planPrice = "295 Kr"
			this.planSession = "4"
			} else {
			this.planTitle = "Pro"
			this.planPrice = "495 Kr"
			this.planSession = "6"}
	}
	private mounted(): void {
		if (!!this.feedbackType || !this.isEmailVerified) this.modal = true
	}
	private getSystemTime(): Promise<string> {
		return Portal.client.call.system.Time().response.then(res => res.Time)
		}
	private listUpcoming(listUpcoming: boolean): void {
		this.listBookings = listUpcoming
		this.canceledBookingId = null
		}
	private listTrainers(prevTrainers: boolean): void {
		this.prevTrainers = prevTrainers
	}
	private startBookingSession(booking: IBooking): void {
		this.$emit("startBookingSession", booking)
		}
	private cancelBooking(booking: IBooking): void {
		this.showDefaultPanel()
		this.smPanels = null
		this.canceledBookingId = Number(booking)
		this.$emit("cancelBooking", booking)
	}
	private timeUntilBooking(booking: IBooking): void {
		this.getSystemTime().then(systemTime => {
			this.systemTime = systemTime
			this.booking = booking
			this.selectedBookingId = booking.Id
			this.trainer = null
			this.selectedTrainerId = null
			this.smPanels = true
			})
	}
	private openActiveItem(item: string): void {
		item === "signout" ? this.$router.push("/") : (this.activeItem = item)
	}
	private trainerData(trainer: ITrainerProfile): void {
		this.trainer = trainer
		this.selectedTrainerId = trainer.Id
		this.booking = null
		this.selectedBookingId = null
	}
	private showDefaultPanel(): void {
		this.booking = null
		this.selectedBookingId = null
		this.trainer = null
		this.selectedTrainerId = null
		this.smPanels = null
	}
	private modalConfirmBooking(
		duration: TrainingSessionDuration,
		type: TrainingSessionType,
		termin: string,
		name?: string
		): void {
		this.modalText = `Confirmed ${duration === 30 ? 25 : 55}m ${this.capitalize(
			TrainingSessionType[type]
		)} session ${name ? "with " + name : ""} for ${termin}`
		this.modal = true
	}
	private modalErr(msg: string): void {
		this.showDefaultPanel()
		this.modalText = msg
		this.modal = true
		this.modalType = "error"
	}
	private modalEmailVerify(modalText: string): void {
		this.modalText = modalText
		this.modal = true
	}
	private closeModal(): void {
		this.modal = null
		this.modalType = null
	}
	private sendPlanInfo(): void {
		this.$router.push("/signup")
		if (this.planTitle === "Basic")
			this.subscriptionCode = "subscription-a"
	}
	private capitalize(str: string): string {
		return str.charAt(0).toUpperCase() + str.slice(1)
	}
	private bgBlur(): boolean | null {
		return this.modal || (this.smPanels && document.body.clientWidth < 960)
		}

	private updateRating(session: ITrainingSession): void {
		this.$emit("updateRating", session)
		}

	private closeFeedback(): void {
		this.modal = false
		this.$emit("closeFeedback")
		}}
