

































import {Component, Prop, Vue} from "vue-property-decorator"
import {IStudentProfile, ITrainingSession, TrainingSessionRating, TrainingSessionState} from "@chaosinsight/yogipeople-portalclient"
import Portal from "@/managers/portal"
import Zendesk from "@/managers/zendesk.ts"
import i18n from "../i18n"
import { TranslateResult } from "vue-i18n"

@Component({ components: { } })
export default class FeedbackStudent extends Vue {
	@Prop()
	public feedbackType!: string

	@Prop()
	public profile!: IStudentProfile

	@Prop()
	public sessionData!: ITrainingSession

	public maxRating: number = 5
	public rating: TrainingSessionRating | null = null
	public title: TranslateResult | null = null
	public comment: string | null = null
	public cancelTime: string | null = null

	private storage = window.localStorage

	private cancelReasons: TranslateResult[] = [
		i18n.t("feedbackStudent.reason1"),
		i18n.t("feedbackStudent.reason2"),
		i18n.t("feedbackStudent.reason3"),
		i18n.t("feedbackStudent.reason4"),
		i18n.t("feedbackStudent.reason5")
	]

	private created(): void {
		this.cancelTime = this.feedbackType === "cancel" ? new Date().toLocaleString() : null
		this.title = ([TrainingSessionState.Expired, TrainingSessionState.OtherUserCancelled, TrainingSessionState.OtherUserCompleted].indexOf(this.sessionData.State) !== -1)
			? i18n.t("feedbackStudent.text6")
			: i18n.t("feedbackStudent.text7")

		// Zendesk ticket "session started": remove local storage item if exists
		if (this.storage.getItem("zendesk") !== null)
			this.storage.removeItem("zendesk")
	}

	/**
	 * Submit corresponding feedback
	 */

	private submitFeedback(cancelReason?: string) {
		if ([TrainingSessionState.Expired, TrainingSessionState.OtherUserCompleted].indexOf(this.sessionData.State) !== -1)
			this.completeOnLogin()
		else if (this.sessionData.State === TrainingSessionState.OtherUserCancelled)
			this.cancelOnLogin()
		else
			!!cancelReason ? this.postZendesk(cancelReason) : this.rate()
	}

	/**
	 * Complete/cancel session on login (if exists)
	 */

	private completeOnLogin(): void {

		Portal.client.call.trainingSession.studentComplete(this.sessionData.Id, this.rating)
			.response.then(() => {
				this.postZendesk()
			})
	}

	private cancelOnLogin(): void {
		Portal.client.call.trainingSession.cancel(this.sessionData.Id)
			.response.then(() => {
				this.postZendesk()
			})
	}

	/**
	 * Post ratings
	 */

	private rate(): void {
		if (this.rating === null)
			return

		Portal.client.call.trainingSession.studentRate(this.sessionData.Id, this.rating)
			.response.then(() => {
				if (this.feedbackType === "updateRating")
					this.$emit("reloadHistory")
				this.postZendesk()
			})
	}

	/**
	 * Send Zendesk ticket
	 */

	private postZendesk(cancelReason: string | null = null): void {
		window.localStorage.setItem("closedFeedback", "closed")
		if (cancelReason)

		if (this.feedbackType !== "updateRating")
			Zendesk.createTicket(this.feedbackType, cancelReason, this.profile, this.sessionData, this.cancelTime, this.rating, null, this.comment, true)
		this.$emit("close")
	}
}
