interface IWhichBrowser {
	name: string
	os: string
}

const whichBrowser: IWhichBrowser = {name: "unknown", os: "unknown"}

if (navigator.appVersion.toLowerCase().indexOf("mac") !== -1) whichBrowser.os = "MacOS"
else if (/ipad|iphone|ipod/.test(navigator.platform.toLocaleLowerCase())) whichBrowser.os = "iOS"
else if (window.navigator.userAgent.indexOf("ServiceUI") !== -1) whichBrowser.os = "Windows 10"
else if (window.navigator.userAgent.indexOf("Windows NT 6.2") !== -1) whichBrowser.os = "Windows 8"
else if (window.navigator.userAgent.indexOf("Windows NT 6.1") !== -1) whichBrowser.os = "Windows 7"
else if (window.navigator.userAgent.indexOf("Windows NT 6.0") !== -1) whichBrowser.os = "Windows Vista"
else if (window.navigator.userAgent.indexOf("Windows NT 5.1") !== -1) whichBrowser.os = "Windows XP"
else if (window.navigator.userAgent.indexOf("Windows NT 5.0") !== -1) whichBrowser.os = "Windows 2000"
else if (navigator.appVersion.toLowerCase().indexOf("win") !== -1) whichBrowser.os = "Windows"
else if (navigator.userAgent.toLowerCase().indexOf("android") > -1) whichBrowser.os = "Android"
else if (navigator.appVersion.toLowerCase().indexOf("linux") !== -1) whichBrowser.os = "Linux"
else if (navigator.appVersion.toLowerCase().indexOf("x11") !== -1) whichBrowser.os = "UNIX"

if (navigator.userAgent.toLowerCase().indexOf("chrome") !== -1) whichBrowser.name  = "Chrome"
else if (navigator.userAgent.toLowerCase().indexOf("firefox") !== -1 ) whichBrowser.name = "FireFox"
else if (navigator.userAgent.toLowerCase().indexOf("safari") !== -1) whichBrowser.name = "Safari"
else if (navigator.appVersion.toLowerCase().indexOf("edge") > -1) whichBrowser.name = "Edge"
else if ((navigator.userAgent.toLowerCase().indexOf("msie") !== -1 )) whichBrowser.name = "IE"
else if (navigator.userAgent.toLowerCase().indexOf("op") > -1) whichBrowser.name = "Opera"

export default whichBrowser
