import {Component, Vue} from "vue-property-decorator"
import {IStudentProfile, ITrainerProfile, ITrainingSession} from "@chaosinsight/yogipeople-portalclient"
import whichBrowser from "@/mixins/whichBrowser"

@Component({ components: { } })
class Zendesk extends Vue {
	private url: string = "https://helloyoga.zendesk.com/api/v2/requests.json"
	private token: string = "Bearer a5ec5001e4f565b24562c9152167b756654bd331d8d857a645607c8fd12c7054"

	/**
	 * Media Error ticket: video/audio not detected
	 */

	public postTicketMediaErr(profile: IStudentProfile | ITrainerProfile, session: ITrainingSession) {
		fetch(this.url, {
			method: "POST",
			headers: {
				"Content-Type": "application/json",
				"Authorization": this.token
			},
			body: JSON.stringify({
				request: {
					email: profile.Email,
					requester: {
						name: `${profile.FirstName} ${profile.LastName}`
					},
					subject: `Media Error!`,
					comment: {
						body: `
							Problem with accessing to microphone and/or camera!
							------------------------------------------------------
							Session started on: ${new Date(session.StartsOn).toLocaleString()}
							Session expires: ${new Date(session.ExpiresOn).toLocaleString()}
							Session ID: ${session.Id}
							------------------------------------------------------
							Name: ${profile.FirstName} ${profile.LastName}
							Email: ${profile.Email}
							ID: ${profile.Id}
							------------------------------------------------------
							Participant name: ${session.Participant.FirstName} ${session.Participant.LastName}
							------------------------------------------------------
							Browser: ${whichBrowser.name}
							OS: ${whichBrowser.os}
							Platform: ${navigator.platform}
							UserAgent: ${navigator.userAgent}
							`
					}
				}
			})
		})
	}

	/**
	 * Send ticket on session started
	 */

	public postTicketStart(profile: IStudentProfile | ITrainerProfile, session: ITrainingSession) {
		fetch(this.url, {
			method: "POST",
			headers: {
				"Content-Type": "application/json",
				"Authorization": this.token
			},
			body: JSON.stringify({
				request: {
					email: profile.Email,
					requester: {
						name: `${profile.FirstName} ${profile.LastName}`
					},
					subject: `Session Started!`,
					comment: {
						body: `
							Session started on: ${new Date(session.StartsOn).toLocaleString()}
							Session expires: ${new Date(session.ExpiresOn).toLocaleString()}
							Session ID: ${session.Id}
							------------------------------------------------------
							Name: ${profile.FirstName} ${profile.LastName}
							Email: ${profile.Email}
							ID: ${profile.Id}
							------------------------------------------------------
							Participant name: ${session.Participant.FirstName} ${session.Participant.LastName}
							------------------------------------------------------
							Browser: ${whichBrowser.name}
							OS: ${whichBrowser.os}
							Platform: ${navigator.platform}
							UserAgent: ${navigator.userAgent}
							`
					}
				}
			})
		})
	}

	/**
	 * Send ticket on session aborted, canceled (ended earlier), completed
	 */

	// tslint:disable:max-line-length
	public createTicket(feedbackType: string, reason: string | null, profile: IStudentProfile | ITrainerProfile, session: ITrainingSession, endTime: string | null, stars: number | null, blockStudent: boolean | null, msg: string | null, isStudent?: boolean): void {
		fetch(this.url, {
			method: "POST",
			headers: {
				"Content-Type": "application/json",
				"Authorization": this.token
			},
			body: this.getBody(feedbackType, reason, profile, session, endTime, stars, blockStudent, msg, isStudent)
		})
	}

	private getBody(feedbackType: string, reason: string | null, profile: IStudentProfile | ITrainerProfile, session: ITrainingSession, endTime: string | null, stars: number | null, blockStudent: boolean | null, msg: string | null, isStudent?: boolean): string {
		const requester = isStudent ? "Student" : "Trainer"
		const participant = isStudent ? "Trainer" : "Student"

		return JSON.stringify({
			request: {
				email: profile.Email,
				requester: {
					name: `${profile.FirstName} ${profile.LastName}`
				},
				subject: `${this.getSubject(feedbackType, endTime)}`,
				comment: {
					body: `
						${requester} name: ${profile.FirstName} ${profile.LastName}
						${requester} email: ${profile.Email}
						${requester} ID: ${profile.Id}
						------------------------------------------------------
						${participant} name: ${session.Participant.FirstName} ${session.Participant.LastName}
						------------------------------------------------------
						Session ID: ${session.Id}
						Session start: ${new Date(session.StartsOn).toLocaleString()}
						Session expires: ${new Date(session.ExpiresOn).toLocaleString()}
						------------------------------------------------------
						Session success: YES
						${this.isEndedEarlier(feedbackType, endTime)}
						------------------------------------------------------
						${this.getFeedback(feedbackType, stars, blockStudent, reason)}
						------------------------------------------------------
						${this.getFeedbackComment(msg)}
						------------------------------------------------------
						Browser: ${whichBrowser.name}
						OS: ${whichBrowser.os}
						Platform: ${navigator.platform}
						UserAgent: ${navigator.userAgent}
					`
				}
			}
		})
	}

	private getSubject(feedbackType: string, endTime: string | null): string {
		if (feedbackType === "completeOnLogin")
			return `Session completed on login`
		else if (feedbackType === "cancelOnLogin")
			return `Session completed on login (ended earlier)`
		return endTime
			? `Session complete (ended earlier)`
			: `Session complete`
	}

	private isEndedEarlier(feedbackType: string, endTime: string | null) {
		if (feedbackType === "completeOnLogin")
			return `Session fully completed (rated on next login)`
		else if (feedbackType === "cancelOnLogin")
			return `Session completed earlier on next login`
		return endTime
			? `Session ended earlier on: ${endTime}`
			: `Session fully completed`
	}

	private getFeedback(feedbackType: string, stars: number | null, blockStudent: boolean | null, reason: string | null): string | null {
		if (feedbackType === "complete" || feedbackType === "completeOnLogin")
			if (blockStudent === null)
				return stars
					? `Rating: ${stars} ⭐️`
					: `Rating not submited`
				else
					return `Allow another session: ${!blockStudent ? "YES" : "NO"}`
		else
			return reason
				? `Reason selected: ${reason}`
				: `Reason not selected`
	}

	private getFeedbackComment(msg: string | null): string {
		return msg
			? `Feedback message: ${msg}`
			: `Feedback message: No message`
	}
}

export default new Zendesk()
